@-webkit-keyframes JoinLetter{
  0%{
    opacity:0;
    letter-spacing:1.6rem
  }

  100%{
    opacity:1;
    letter-spacing:0.2rem
  }
}

@keyframes JoinLetter{
  0%{
    opacity:0;
    letter-spacing:1.6rem
  }

  100%{
    opacity:1;
    letter-spacing:0.2rem
  }
}

.home-layout{
  min-height:100vh;
  width:100%;
  overflow:hidden;
  padding-top:6rem;
  padding-bottom:6rem;
  display:flex;
  align-items:center;
  justify-content:center
}

@media (min-width: 992px){
  .home-layout{
    padding-top:3rem;
    padding-bottom:0px
  }
}

.home-layout .swiper-col .swiper{
  width:340px;
  height:420px;
  padding-left:1.5rem;
  padding-right:1.5rem
}

@media (min-width: 680px){
  .home-layout .swiper-col .swiper{
    width:660px
  }
}

@media (min-width: 800px){
  .home-layout .swiper-col .swiper{
    width:700px
  }
}

@media (min-width: 992px){
  .home-layout .swiper-col .swiper{
    overflow:visible;
    width:340px
  }
}

@media (min-width: 1537px){
  .home-layout .swiper-col .swiper{
    width:440px;
    height:520px
  }
}

.home-layout .swiper-col .swiper .swiper-wrapper{
  height:90%;
  width:100%
}

.home-layout .swiper-col .swiper .swiper-pagination{
  left:0;
  bottom:0
}

.home-layout .swiper-col .swiper .swiper-pagination .swiper-pagination-bullet{
  margin-bottom:0 !important
}

.home-layout .swiper-col .swiper .swiper-pagination-bullet-active{
  --tw-bg-opacity:1;
  background-color:rgba(41, 7, 89, var(--tw-bg-opacity))
}

.home-layout .swiper-col .swiper .swiper-button-next:after,.home-layout .swiper-col .swiper .swiper-button-prev:after{
  color:#290759;
  font-size:25px
}

.home-layout .swiper-col .swiper .angle{
  position:absolute;
  z-index:10;
  width:1.5rem;
  cursor:pointer;
  --tw-text-opacity:1;
  color:rgba(41, 7, 89, var(--tw-text-opacity));
  animation-duration:var(--animate-duration);
  animation-fill-mode:both
}

@media print, (prefers-reduced-motion: reduce){
  .home-layout .swiper-col .swiper .angle{
    animation-duration:1ms;
    transition-duration:1ms;
    animation-iteration-count:1
  }
}

.home-layout .swiper-col .swiper .angle{
  animation-name:fadeIn;
  top:42%;
}

.home-layout .swiper-col .swiper .angle.angle-right{
  right:0px
}

.home-layout .swiper-col .swiper .angle.angle-left{
  left:0px
}

.home-layout .swiper-col .swiper .angle.last{
  display:none
}

.home-layout .swiper-col .swiper .angle.first{
  display:none
}

.home-layout .swiper-col .swiper-slide{
  position:relative;
  display:flex;
  flex-direction:column;
  border-radius:1rem;
  padding-left:1.5rem;
  padding-right:1.5rem;
  padding-top:3rem;
  padding-bottom:3rem;
  text-align:center;
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity));
  --tw-blur:blur(12px);
  --tw-backdrop-blur:var(--tw-empty,/*!*/ /*!*/);
  --tw-backdrop-brightness:var(--tw-empty,/*!*/ /*!*/);
  --tw-backdrop-contrast:var(--tw-empty,/*!*/ /*!*/);
  --tw-backdrop-grayscale:var(--tw-empty,/*!*/ /*!*/);
  --tw-backdrop-hue-rotate:var(--tw-empty,/*!*/ /*!*/);
  --tw-backdrop-invert:var(--tw-empty,/*!*/ /*!*/);
  --tw-backdrop-opacity:var(--tw-empty,/*!*/ /*!*/);
  --tw-backdrop-saturate:var(--tw-empty,/*!*/ /*!*/);
  --tw-backdrop-sepia:var(--tw-empty,/*!*/ /*!*/);
  backdrop-filter:var(--tw-backdrop-blur) var(--tw-backdrop-brightness) var(--tw-backdrop-contrast) var(--tw-backdrop-grayscale) var(--tw-backdrop-hue-rotate) var(--tw-backdrop-invert) var(--tw-backdrop-opacity) var(--tw-backdrop-saturate) var(--tw-backdrop-sepia);
  -webkit-backdrop-filter:blur(12px);
  backdrop-filter:blur(12px)
}

.home-layout .swiper-col .swiper-slide::before{
  backdrop-filter:blur(12px) contrast(60%);
  -webkit-backdrop-filter:blur(12px) contrast(60%);
  width:contain
}

.home-layout .swiper-col .swiper-slide img{
  margin-left:auto;
  margin-right:auto;
  margin-top:auto;
  margin-bottom:auto
}

.home-layout .swiper-col .swiper-slide h2{
  font-size:1.25rem;
  line-height:1.75rem;
  font-weight:700
}

.home-layout .swiper-col .swiper-slide p{
  margin-top:1rem
}

.home-layout .swiper-col .swiper-slide .swiper-slide-shadow{
  display:none
}

.home-layout .swiper-col .swiper-slide:nth-child(1n){
  background-color:rgba(154,121,186,0.85)
}

.home-layout .swiper-col .swiper-slide:nth-child(2n){
  background-color:rgba(161,201,188,0.85)
}

.home-layout .swiper-col .swiper-slide:nth-child(3n){
  background-color:rgba(39,36,98,0.85)
}

.home-layout .info-col{
  height:100%;
  text-align:center;
  order:1
}

@media (max-width: 992px){
  .home-layout .info-col{
    order:-1;
    margin-bottom:5rem
  }
}

.home-layout .info-col h1{
  margin-top:4rem;
  margin-bottom:3rem;
  font-family:ui-sans-serif, system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji";
  font-size:3rem;
  line-height:1;
  font-weight:700;
  --tw-text-opacity:1;
  color:rgba(0, 0, 0, var(--tw-text-opacity))
}

@media (min-width: 481px){
  .home-layout .info-col h1{
    margin-bottom:5rem;
    font-size:4.5rem;
    line-height:1
  }
}

.home-layout .info-col .get-share-buttons{
  width:15rem;
  border-radius:0.5rem;
  background-color:transparent
}

.home-layout .info-col .get-share-buttons:hover{
  --tw-bg-opacity:1;
  background-color:rgba(41, 7, 89, var(--tw-bg-opacity))
}

.home-layout .info-col .get-share-buttons{
  padding-top:0.625rem;
  padding-bottom:0.625rem;
  font-weight:500;
  --tw-text-opacity:1;
  color:rgba(41, 7, 89, var(--tw-text-opacity))
}

.home-layout .info-col .get-share-buttons:hover{
  --tw-text-opacity:1;
  color:rgba(255, 255, 255, var(--tw-text-opacity))
}

.home-layout .info-col .get-share-buttons .rs-ripple-pond{
  border-radius:0.5rem;
  border-width:2px;
  --tw-border-opacity:1;
  border-color:rgba(41, 7, 89, var(--tw-border-opacity))
}

